<template>
  <div style="display: inline-block" id="uploadCommon">
    <a-button type="primary"  style="background: rgb(122,110,188)" @click="showModal">{{bname}}</a-button>
    <a-modal
      :title="bname"
      :footer="null"
      v-model="modal">
       <div class="clearfix">
         <hr />
         <div style="margin-top: 25px">
           <span>选择文件:</span>
           <span class="upload">
             <a-upload
           :fileList="fileList"
           :remove="handleRemove"
           :beforeUpload="beforeUpload">
           <a-button style="background: #dddddd;border: none">
             <a-icon type="upload"/> 选择文件
           </a-button>
         </a-upload>
             <span v-if="fileList.length === 0">未选择任何文件</span>
           </span>
           <a-button
             type="primary"
             @click="handleUpload"
             :disabled="fileList.length === 0"
             :loading="uploading"
             :style="fileList.length === 0?{background:'#69747c',marginLeft:'5px'}:{background:'rgb(122,110,188)',marginLeft:'5px'}">
             {{uploading ? '上传中...' : '开始上传' }}
           </a-button>
         </div>
       </div>
    </a-modal>
  </div>
</template>

<script>
  import {OutdoorTestApi} from '@/http/api/outdoorTest'
  export default {
    name: '',
    props:['bname'],
    data() {
      return {
        modal:false,
        fileList: [],
        uploading: false,
      };
    },
    beforeCreate() {

    },
    computed:{
    },
    methods: {
      showModal() {
        this.modal = true;
      },
      handleRemove(file) {
        const index = this.fileList.indexOf(file);
        const newFileList = this.fileList.slice();
        newFileList.splice(index, 1);
        this.fileList = newFileList
      },
      beforeUpload(file) {
        this.fileList = [...this.fileList, file]
        return false;
      },
      handleUpload() {
        const { fileList } = this;
        const formData = new FormData();
        fileList.forEach((file) => {
          formData.append('file', file);
        });
        let ajax;
        if(fileList.length==1){
          this.uploading = true;
          ajax = OutdoorTestApi.importOutHomeLog(formData);
          ajax.then(x=>{
            if(x.data.success){
              this.uploading = false
              this.fileList = []
              this.modal=false;
              this.$emit('query')
              this.$message.success('导入成功！')
            }else{
              this.uploading = false;
            }
          })
        }else{
          this.$message.warn('请选择一个文件！')
        }
      }
    }
  }
</script>

<style>
  @import '../../static/css/style.css';
  .ant-upload-list {
    display: inline-block;
  }
  .ant-upload-list-item-name{
    vertical-align: middle;
    width:120px!important;
  }
  .upload{
    background-color: #fff;
    border: 1px solid lightsteelblue;
    padding: 10px 14px;
    box-shadow: 0px 5px 5px #f0f0f0;
    border-radius: 5px;
  }
</style>



